import React from "react";
import axios from "axios";
import { useState, useEffect } from "react";
// import BookInfoModal from "./BookInfoModal";
function Genre(props) {
  const [bookData, setBookData] = useState([]);
  useEffect(() => {
    getBooksData();
  }, []);
  const getBooksData = () => {
    axios
      .get(`https://api.itbook.store/1.0/search/${props.genres}`)
      .then((response) => {
        const data = response.data;
        setBookData(data.books);
      });
  };
  return (
    <>
      <h1 className="genres-headingg">{props.genres}</h1>
      <div className=" d-flex flex-row flex-gap-2 justify-content-center align-items-center flex-wrap">
        {bookData.map((item) => {
          return (
            <div className="genre-distributed-card m-3 p-3 individual-books-card">
              <img src={item.image} alt="" />
              <p>{item.title}</p>
              <p>Price : {item.price}</p>
              <button
                className="btn btn-light"
                onClick={() => {
                  props.addToCart(item);
                }}
              >
                Add to cart
              </button>
            </div>
          );
        })}
        {/* <BookInfoModal modalOpen={modal} /> */}
      </div>
    </>
  );
}

export default Genre;
