import "./App.css";
import Homepage from "./components/Homepage";
import Searchbar from "./components/Searchbar";
import SideBar from "./components/Sidebar";
import PersonList from "./components/PersonList.js";
import Genre from "./components/Genre";
import React, { useState } from "react";
import { HashRouter, Route, Routes } from "react-router-dom";
import Modal from "./components/Modal";
import Cart from "./components/Cart";


function App() {
  const [genres, setGenres] = useState("Dephault");
  const [totalPrice, seTotalPrice] = useState(0);
  const [cart,addToCart]=useState([]);

  const changeCart = (value) => {
    const isPresent = cart.find((item) => item.isbn13 === value.isbn13);
    if (isPresent) {
      addToCart(
        cart.map((item) =>
          item.isbn13 === value.isbn13
            ? { ...isPresent, quantity: isPresent.quantity + 1 }
            : item
        ));
    } else {
      addToCart([...cart, { ...value, quantity: 1 }]);
    }
  };


  const removeFromCart = (value) => {
    const isPresent = cart.find((item) => item.isbn13 === value.isbn13);
    if (isPresent.quantity === 1) {
      addToCart(cart.filter((item) => item.isbn13 !== value.isbn13));
    } else {
      addToCart(
        cart.map((item) =>
          item.isbn13 === value.isbn13
            ? { ...isPresent, quantity: isPresent.quantity - 1 }
            : item
        )
      );
    }
  };

  const changeGenres = (s) => {
    // alert(s);
    setGenres(s);
  };
  return (
    <div id="App">
            <SideBar />
      <HashRouter>
        <>
          <Routes>
            {/* <SideBar /> */}
            <Route
              path="/"
              element={<Homepage changeGenres={changeGenres} />}
            />
            <Route
              path="/"
              element={<SideBar/>}
            />
            <Route exact path="genre" element={<Genre genres={genres} cart={cart} addToCart={changeCart} />} />
            <Route path="modal" element={<Modal />} />
            <Route path="cart" element={<Cart cart={cart}
                addToCart={changeCart}
                removeFromCart={removeFromCart} />} />
          </Routes>
        </>
      </HashRouter>
      <div className="blank_space"></div>
    </div>
  );
}

export default App;
