import React from "react";
import "./Homepage.css";
import { Link } from "react-router-dom";
import sideBar from "./Sidebar";

function Homepage(props) {
  return (
    <div>
      <div className="top-section">
        <h1>Lots of eBooks. 100 percent free.</h1>
        <h6>
          Welcome to your friendly neighbourhood library. We have more than
          zsedrr
        </h6>
      </div>
      <div className="top-section"></div>

      <div className="genres">
        <h1>Browse Genres</h1>
        <div className="genres-items">
          <div className="item item-1">
            <div className="heading">
              <Link
                style={{ textDecoration: "none", color: "white" }}
                to="genre"
                onClick={() => {
                  props.changeGenres("Mathematics");
                }}
              >
                {/* <Link to="genre"> */}
                <h2> Mathematics</h2>
              </Link>
            </div>
          </div>
          <div className="item item-2">
            <div className="heading">
              <Link
                style={{ textDecoration: "none", color: "white" }}
                to="genre"
                onClick={() => {
                  props.changeGenres("Physics");
                }}
              >
                {/* <Link to="genre"> */}
                <h2> Physics</h2>
              </Link>
            </div>
          </div>
          <div className="item item-3">
            <Link
              style={{ textDecoration: "none", color: "white" }}
              to="genre"
              onClick={() => {
                props.changeGenres("Chemistry");
              }}
            >
              <div className="heading">
                <h2>Chemistry</h2>
              </div>
            </Link>
          </div>
          <div className="item item-4">
            <Link
              style={{ textDecoration: "none", color: "white" }}
              to="genre"
              onClick={() => {
                props.changeGenres("Computer");
              }}
            >
              <div className="heading">
                <h2>Computer</h2>
              </div>
            </Link>
          </div>
          <div className="item item-5">
            <div className="heading">
              <h2>Historic Fiction</h2>
            </div>
          </div>
          <div className="item item-6">
            <div className="heading">
              <h2>Young Adult</h2>
            </div>
          </div>
          <div className="item item-7">
            <div className="heading">
              <h2>Children's</h2>
            </div>
          </div>
          <div className="item item-8">
            <div className="heading">
              <h2>Fantasy</h2>
            </div>
          </div>
        </div>
        <div className="cart-div">
          {/* <h6>My Cart</h6> */}
          <Link style={{ textDecoration: "none", color: "white" }} to="cart">
            <h6>My Cart</h6>
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Homepage;
